.shops-section {
  padding: var(--padding-medium) var(--padding-sides-small);
  .content {
    width: 64vw;
    margin: auto;
    @media (max-width: $xxlarge) {
      width: 90vw;
    }
    .section-title {
      margin-bottom: var(--padding-medium);
      h1 {
        font-size: 1.8em;
      }
    }
    .shops {
      --cols: 4;
      display: grid;
      grid-template-columns: repeat(var(--cols), 1fr);
      grid-gap: 1.1vw;
      @media (max-width: $medium) {
        --cols: 2;
      }
      @media (max-width: $xsmall) {
        --cols: 1;
      }
      .item {
        border: .14vw solid $color-lred2;
        padding: var(--padding-small);
        display: grid;
        grid-template-rows: 6.6vw 5vw 6.2vw;
        text-align: center;
        line-height: 1;
        font-size: .8vw;
        @media (max-width: $medium) {
          font-size: 1em;
          grid-template-rows: auto;
        }
        &.one-group {
          grid-template-rows: auto;
        }
        p {
          margin-bottom: .2vw;
        }
        .ico {
          width: 5vw;
          height: 3.2vw;
          @media (max-width: $medium) {
            width: 7vw;
            height: 5vw;
          }
          @media (max-width: $xsmall) {
            width: 42px;
            height: 42px;
          }
        }
        .op-hours {}
      }
    }
  }
}
