.full-width-photos {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  .item {
    position: relative;
    &:after {
      content: '';
      display: block;
      padding-bottom: 70%;
    }
    img {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
