.motto-section {
  padding: var(--padding-large) var(--padding-sides-small);
  .content {
    width: 55vw;
    margin: auto;
    text-align: center;
    @media (max-width: $xsmall) {
      width: 100%;
    }
    img {
      width: 10vw;
      margin: auto;
      margin-bottom: var(--padding-medium);
      @media (max-width: $xsmall) {
        width: 100px;
      }
    }
    .h2 {
      span {
        color: $color-green;
      }
    }
  }
}
