.price-box {
  background-color: $color-brown;
  color: $color-white;
  display: inline-flex;
  align-items: flex-end;
  justify-content: center;
  text-align: center;
  padding: .7vw 2.2vw;
  border-radius: 3vw;
  @media (max-width: $xlarge) {
    font-size: .8em;
    padding: .6vw 2vw;
  }
  span {
    font-size: 1.3em;
    font-weight: 500;
    margin-right: .3vw;
    margin-bottom: .1vw;
  }
  .price {
    font-size: 2.7em;
    font-weight: 600;
    &.no-price {
      font-size: 1.3em;
      font-weight: 500;
      text-transform: uppercase;
    }
    &.sale {
      display: flex;
      align-items: center;
      del {
        margin-right: .6vw;
        font-size: .6em;
      }
      ins {
        text-decoration: none;
      }
    }
  }
}
