.wc-register-section {
  padding: var(--padding-large) var(--padding-sides-small);
  .box {
    max-width: 800px;
    margin: auto;
    .section-title {
      margin-bottom: var(--padding-small);
    }
    form {
      grid-template-areas:
        "firstname lastname"
        "email password"
        // "company-name company-name"
        "shipping-address shipping-address"
        "billing-address billing-address"
        "submit submit";
      .input-wrapper {
        &.firstname {
          grid-area: firstname;
        }
        &.lastname {
          grid-area: lastname;
        }
        &.email {
          grid-area: email;
        }
        &.password {
          grid-area: password;
        }
        &.company-name {
          // grid-area: company-name;
        }
        &.submit {
          grid-area: submit;
          button {
            margin: auto;
            display: block;
          }
        }
        &.lost-pass {
          display: flex;
          justify-content: center;
        }
      }
      .input-group {
        &.billing-address {
          grid-area: billing-address;
          .fields-wrapper {
            // grid-template-columns: repeat(3, 1fr);
            .input-wrapper {
              &.address-1 {
                // grid-column: span 2;
              }
              &.address-2 {
                // grid-column: span 1;
              }
              &.city {
                // grid-column: span 2;
              }
              &.post-code {
                // grid-column: span 2;
              }
              &.country {
                // grid-column: span 2;
              }
            }
          }
        }
      }
    }
  }
}
