.map-section {
  padding: var(--padding-sides-small);
  .map-wrapper {
    width: 64vw;
    margin: auto;
    position: relative;
    @media (max-width: $xxlarge) {
      width: 90vw;
    }
    &:before {
      content: '';
      display: block;
      padding-bottom: 32%;
      @media (max-width: $large) {
        padding-bottom: 64%;
      }
    }
    .gmap {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}
