.page-header {
  padding: var(--padding-small);
  margin-bottom: var(--padding-small);
  .content {
    max-width: 50vw;
    margin: auto;
    // @include content;
    text-align: center;
    @media (max-width: $xsmall) {
      max-width: none;
      // font-size: .6em;
    }
    .icon {
      margin-bottom: -1vw;
      position: relative;
      .ico-shop {
        --size: 8vw;
        width: var(--size);
        height: var(--size);
      }
      &:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: linear-gradient(0deg, $color-white, transparent 60%);
      }
    }
    h1,
    h2 {
      position: relative;
      font-weight: 900;
      letter-spacing: .3vw;
      text-transform: uppercase;
      color: $color-lred2;
    }
    h1 {
      font-size: 5em;
      @media (max-width: $xsmall) {
        font-size: 3em;
      }
    }
    h2 {
      font-size: 2.8em;
    }
    p {
      font-weight: 500;
      font-size: 1.2em;
    }
  }
}
