.breadcrumbs {
  padding: var(--padding-small);
  .content {
    @include content;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: 1.2em;
        opacity: .8;
        &:first-child {
          &:before {
            content: none;
          }
        }
        &:before {
          --size: 1vw;
          content: '>';
          display: block;
          width: var(--size);
          height: var(--size);
          margin: 0 .1vw;
          position: relative;
          top: .5vw;
          line-height: 0;
          text-align: center;
          @media (max-width: $medium) {
            --size: 2vw;
            top: 1vw;
          }
          @media (max-width: $xsmall) {
            --size: 16px;
            top: 9px;
          }
        }
        a {
          display: block;
          color: $color-text;
          &:hover {
            color: $color-lred;
          }
        }
      }
    }
  }
}
