.text-category {
  position: relative;
  padding-top: var(--padding-large);
  @media (max-width: $small) {
    background-color: $color-lred2;
    padding-bottom: var(--padding-medium);
  }
  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 30vw;
    background-color: $color-lred2;
    @media (max-width: $small) {
      content: none;
    }
  }
  .section-title {
    position: relative;
    h1 {
      strong {
        color: $color-white;
      }
    }
  }
  .category-select {
    position: relative;
    @media (max-width: $small) {
      padding-top: var(--padding-small);
    }
  }
}
