.product-box {
  display: flex;
  padding: 0 var(--padding-medium);
  &.sale-product {
    order: -2;
  }
  &.featured-product {
    order: -1;
  }
  a {
    display: block;
    color: $color-text;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    .badge {
      background-color: $color-white;
      padding: .14vw;
      border-radius: 50%;
      --size: 6.2vw;
      height: var(--size);
      width: var(--size);
      display: flex;
      position: absolute;
      left: 0;
      top: 0;
      @media (max-width: $xlarge) {
        font-size: .8em;
      }
      @media (max-width: $medium) {
        --size: 10vw;
      }
      @media (max-width: $xsmall) {
        --size: 70px;
      }
      &.new {}
      &.sale {
        .text {
          background-color: $color-lgreen;
        }
      }
      .text {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background-color: $color-lred2;
        p {
          text-transform: uppercase;
          font-weight: 700;
          color: $color-white;
        }
      }
    }
    img {
      width: 100%;
      height: 20vw;
      object-fit: contain;
      @media (max-width: $xsmall) {
        height: 140px;
      }
    }
    h1 {
      color: $color-brown;
      font-size: 2em;
      text-transform: uppercase;
      font-weight: 700;
      margin-bottom: var(--padding-small);
      text-align: center;
      min-height: 3vw;
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: $xlarge) {
        font-size: 2.2em;
        min-height: 5vw;
      }
      @media (max-width: $medium) {
        font-size: 1.8em;
        height: 8vw;
      }
      @media (max-width: $xsmall) {
        height: auto;
      }
    }
    .price-box {}
  }
}
