.wc-form {
  display: grid;
  // grid-template-columns: repeat(2, 1fr);
  grid-template-columns: [start] 1fr [center] 1fr [end];
  grid-gap: var(--padding-small) var(--padding-medium);
  
  .form-row {
    display: flex;
    flex-direction: column;
  }
  .form-row-wide {
    grid-column: start/end;
  }
  .input-wrapper {}
  .input-group {
    display: grid;
    grid-gap: var(--padding-small);
    grid-column: start/end;
    .label {
      padding-top: 15px;
      p {
        margin-bottom: 0;
        font-weight: 600;
      }
    }
    // .fields-wrapper {
    //   display: grid;
    //   grid-template-columns: repeat(2, 1fr);
    //   grid-gap: var(--padding-small) var(--padding-medium);
    //   .input-wrapper {}
    // }
  }
}

#ship-to-different-address {
  padding: var(--padding-small) 0;
  label {
    font-size: .8em;
  }
}

#customise_checkout_field, #order_comments_field,
.wc-form .form-row {
  label {
    font-size: .8em;
  }
}

label {
  abbr {
    text-decoration: none;
    color: $color-lred;
  }
}
.optional {
  display: none;
}

