#main-footer {
  padding: var(--padding-large) var(--padding-sides-small) var(--padding-medium);
  background-color: $color-gray;
  color: $color-white;
  .content {
    @include content;
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-areas:
      "text nav"
      "legal legal";
    grid-gap: 3.2vw 2vw;
    @media (max-width: $small) {
      align-items: flex-start;
    }
    @media (max-width: $xsmall) {
      grid-template-columns: 1fr;
      grid-template-areas:
        "text"
        "nav"
        "legal";
      text-align: center;
    }
    .text {
      --cols: 2;
      grid-area: text;
      display: grid;
      grid-gap: var(--padding-small);
      grid-template-columns: repeat(var(--cols), 1fr);
      @media (max-width: $small) {
        --cols: 1;
      }
      @media (max-width: $xsmall) {
        font-size: .8em;
      }
      p {
        span {
          color: lighten($color-primary, 12%)
        }
      }
    }
    .nav {
      --cols: 2;
      grid-area: nav;
      justify-items: flex-end;
      align-items: flex-end;
      display: grid;
      grid-template-columns: repeat(var(--cols), 1fr);
      @media (max-width: $small) {
        --cols: 1;
        align-items: flex-start;
        justify-items: flex-start;
      }
      @media (max-width: $xsmall) {
        align-items: center;
        justify-items: center;

        display: none;
      }
      .item {
        ul {
          display: grid;
          grid-gap: 1.2vw;
          &:before {
            content: '';
            display: block;
            width: 2vw;
            height: .1vw;
            background-color: $color-lgray;
            opacity: .7;
          }
          li {
            a {
              display: block;
              color: $color-white;
            }
          }
        }
      }
    }
    .legal {
      display: flex;
      justify-content: flex-end;
      grid-area: legal;
      padding-bottom: var(--padding-small);
      @media (max-width: $xsmall) {
        justify-content: center;
      }
      a {
        color: $color-white;
      }
    }
  }
}
