.hero {
  .swiper-container {
    .swiper-slide {
      min-height: 40vw;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      @media (max-width: $xsmall) {
        min-height: 320px;
        flex-direction: column;
      }
      .bg {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        @media (max-width: $xsmall) {
          position: static;
          width: 100%;
        }
        .mobile, .desktop {
          @include bg-center;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
        }
        .desktop {
          @media (max-width: $xsmall) {
            opacity: 0;
          }
        }
        .mobile {
          opacity: 0;
          @media (max-width: $xsmall) {
            opacity: 1;
            position: static;
            height: 436px;
            width: 100%;
          }
        }
      }
      .text {
        position: relative;
        z-index: 10;
        padding: 5vw var(--padding-sides-small);
        width: 68vw;
        font-size: 1vw;
        @media (max-width: $small) {
          font-size: 16px;
        }
        @media (max-width: $xsmall) {
          width: 100%;
          font-size: 16px;
          margin-top: -80px;
          text-align: center;
        }
        h1 {
          font-size: 2.5em;
          font-size: 2vw;
          font-weight: 300;
          color: $color-text;
          @media (max-width: $small) {
            font-size: 2em;
          }
          span {
            margin-top: var(--padding-small);
            text-transform: uppercase;
            font-size: 5em;
            font-weight: 900;
            letter-spacing: 1.4rem;
            color: $color-brown;
            @media (max-width: $small) {
              font-size: 2em;
              letter-spacing: 1px;
            }
          }
        }
        h3 {
          @media (max-width: $small) {
            font-size: 1em;
          }
        }
        p {
          font-size: 1.4em;
          width: 42%;
          @media (max-width: $xsmall) {
            width: 100%;
          }
        }
      }
    }
    .swiper-pagination {}
  }
}
