.checkout-section {

  .checkout-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .back {}
    .payment {}
  }



  .form-row {
    display: flex;
    flex-direction: column;
  }
  @include wc-section-padding;
  .content {
    max-width: 800px;
    margin: auto;
    .form-row.place-order {
      display: flex;
      justify-content: center;
    }
    .woocommerce-billing-fields {
      h3 {}
      &__field-wrapper {
        .form-row {
          &.address-field {}
          &#billing_address_1_field,
          &#billing_address_2_field {
            grid-column: start/end;
          }
          label {
          }
          .woocommerce-input-wrapper {}
        }
      }
    }
    .woocommerce-shipping-fields {
      &__field-wrapper {
        @extend .wc-form;
        #shipping_address_1_field,
        #shipping_address_2_field {
          grid-column: start/end;
        }
      }
    }
    #order_review_heading {}
    #order_review {
      p {
        font-size: 1rem;
      }
      label {
        font-weight: 600;
      }
      li {
        padding-left: 0;
        &:before {
          content: none;
        }
      }
      .cart_item {
        .product-name {
          font-size: 1.6em;
          color: $color-lred;
          .extra-data {
            font-size: .8rem;
            color: $color-text;
            p {
              margin: 0;
            }
            ul {
              li {
                &:before {
                  content: none;
                }
              }
            }
          }
          .extra-data-photo {
            img {
              margin: auto;
              max-width: 100%;
            }
          }
          .product-quantity {
            // font-size: 2rem;
          }
        }
        .product-total {
          font-size: 1.6em;
          font-weight: 600;
        }
      }
      .cart-subtotal {
        font-size: 1.6em;
      }
      .woocommerce-shipping-totals {}
      .order-total {
        font-size: 2em;
      }
    }
    .woocommerce-order-details {
      li {
        padding-left: 0;
        &:before {
          content: none;
        }
      }
      h2 {}
      table {}
      .product-total {
        font-size: 1.6em;
        font-weight: 600;
      }
      .product-name {
        a {
          font-size: 1.4em;
          font-weight: 600;
          .product-quantity {}
        }
        .wc-item-meta {
          font-size: .8rem;
        }
      }

    }
  }
}
