.icon-text {
  display: grid;
  grid-template-columns: 6vw auto;
  grid-gap: var(--padding-small);
  @media (max-width: $small) {
    display: flex;
    flex-direction: column;
    text-align: center;
    // grid-template-columns: 50px auto;
  }
  .icon {
    .ico {
      width: 100%;
      height: 4vw;
      @media (max-width: $small) {
        height: 12vw;
      }
      @media (max-width: $xsmall) {
        height: 70px;
        width: 70px;
      }
    }
  }
  .text {}
}
