.mobile-navbar {
  background-color: $color-white;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
  opacity: 0;
  pointer-events: none;
  padding: 70px 15px 30px 50px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &.show {
    opacity: 1;
    pointer-events: all;
  }
  > ul {
    display: grid;
    grid-gap: 10px;
    > li {
      &.open-submenu {
        .sub-menu {
          display: grid;
          padding: 10px 0 14px 15px;
          height: auto;
        }
      }
      &.has-sub-menu {
        position: relative;
        .toggle-submenu {
          @extend .ico;
          @extend .ico-expand;
          --size: 18px;
          height: var(--size);
          width: var(--size);
          display: block;
          position: absolute;
          top: 2px;
          left: -30px;
          font-family: monospace;
          font-weight: 700;
          font-size: 32px;
          line-height: 16px;
          color: $color-primary;
          transform: rotateZ(0deg);
          &.active {
            transform: rotateZ(180deg);
          }
          &:after {
            content: '';
            position: absolute;
            display: block;
            --offset: -10px;
            top: var(--offset);
            left: var(--offset);
            right: var(--offset);
            bottom: var(--offset);
          }
        }
      }
      &.current-menu-item {
        > a {
          color: $color-primary;
        }
      }
      > a {
        font-size: 1.8em;
        color: $color-text;
        padding: var(--padding-tiny);
        display: block;
        margin-bottom: 4px;
      }
      .sub-menu {
        display: grid;
        height: 0;
        overflow: hidden;
        grid-gap: 15px;
        li {
          a {
            font-size: 1.4em;
            color: $color-text;
          }
        }
      }
    }
  }
  .social {
    justify-content: flex-start;
  }
}
