.section-title {
  text-align: center;
  max-width: 50vw;
  margin: auto;
  display: grid;
  grid-gap: var(--padding-medium);
  justify-content: center;
  padding: 0 var(--padding-small);
  @media (max-width: $medium) {
    max-width: 70vw;
  }
  @media (max-width: $xsmall) {
    max-width: none;
    font-size: .9em;
  }

  &.large {
    font-size: 1.8em;
  }
  &.extra-asset {
    padding-top: 2vw;
    h1, .h1 {
      &:before {
        content: '';
        @media (max-width: $small ) {
          content: none;
        }
      }
    }
  }
  &.red {
    strong {
      color: $color-lred;
    }
  }
  &.large {
    font-size: 1.4em;
  }
  h1, .h1 {
    position: relative;
    &:before {
      display: block;
      position: absolute;
      width: 150px;
      height: 70px;
      bottom: 100%;
      left: 0;
      background-image: url('../img/section-title-bg.png');
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
  }
  p.lead {
    font-size: 1.3em;
  }
}
