.button-section {
  padding: var(--padding-large) var(--padding-sides-small);
  @media (max-width: $xsmall) {
    padding: var(--padding-small) var(--padding-sides-small);
  }
  .content {
    @include content;
    text-align: center;
    color: $color-green;
    .icon {
      margin-bottom: -1.2vw;
      position: relative;
      &:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: linear-gradient(0deg, $color-white, transparent 60%);
      }
    }
    p {
      position: relative;
      z-index: 10;
      &.lead {
        font-size: 2.1em;
        font-weight: 500;
        @media (max-width: $xsmall) {
          font-size: 1.6em;
        }
      }
    }
    a.btn {
      margin-top: var(--padding-medium);
    }
  }
}
