.product-list-controls {
  .content {
    @include content;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    > * {
      margin-left: 1vw;
    }
    .woocommerce-result-count {
      font-size: .8em;
      color: lighten($color-text, 40%);
      @media (max-width: $xsmall) {
        font-size: 12px;
      }
    }
    .woocommerce-ordering {
      select {
        border-radius: 50px;
        border-color: lighten($color-text, 40%);
        font-size: .8em;
        color: lighten($color-text, 40%);
        @media (max-width: $xsmall) {
          font-size: 12px;
        }
      }
    }
  }
}
