#developer {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 9999;
  pointer-events: none;
  overflow: hidden;
  display: none;

  &.show {
    display: block;
  }

  .circle {
    pointer-events: all;
    position: absolute;
    padding: 5px;
    z-index: 10;

    .icon {
      cursor: pointer;
      padding: 5px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      background-color: #333;
      opacity: 0.1;
      transition: all 0.2s ease-out;

      &.active {
        background-color: #000;
        opacity: 1;
      }

      &:hover {
        opacity: 1;
      }

      span {
        display: block;
        @extend .ico;
        @extend .ico-developer;
        width: 16px;
        height: 16px;
      }
    }
  }

  .box {
    position: absolute;
    background-color: white;
    max-width: 400px;
    // top: 0;
    left: 0;
    bottom: 0;
    transform: scale(0);
    opacity: 0;
    transition: all 0.2s ease-out;
    transform-origin: 0 100%;
    pointer-events: all;
    box-shadow: 0 0 10px rgba(#333, .3);
    .graph {
      display: none;
      canvas {
        display: block;
        outline: 1px solid #ccc;
        outline-offset: -1px;
        background-color: white;
      }
    }
    .title {
      background-color: #333;
      color: white;
      padding: 8px 10px;
      text-align: center;
      font-weight: 700;
    }

    &.show {
      opacity: 1;
      transform: scale(1);
    }

    .content {
      .feature {
        padding: 10px 15px;
        border-bottom: 1px solid #333;
        display: flex;
        align-items: center;
        justify-content: space-between;

        p {
          margin-right: 10px;
        }

        &:last-child {
          border: 0;
        }
      }
    }

    .foot {
      height: 38px;
      padding: 5px 15px 5px 40px;
      display: flex;
      align-items: center;
      color: white;
      background-color: #333;

      span {
        background-color: black;
        padding: 1px 3px;
      }
    }
  }
}
