.contact-info-image {
  padding: var(--padding-large) var(--padding-sides-small);
  .content {
    max-width: 64vw;
    margin: auto;
    display: grid;
    grid-template-columns: 3fr 5fr;
    grid-gap: var(--padding-large);
    align-items: flex-start;
    @media (max-width: $xxlarge) {
      max-width: 90vw;
    }
    @media (max-width: $small) {
      grid-template-columns: 1fr 1fr;
    }
    @media (max-width: $small) {
      grid-template-columns: auto;
    }
    > .text {
      font-size: 1.1em;
      display: grid;
      grid-gap: var(--padding-small);
      p {
        font-weight: 500;
        &.lead {
          font-size: 1.8em;
          a {
            font-weight: 700;
          }
        }
      }
      .icon-text {}
    }
    .image {
      @media (max-width: $small) {
        display: none;
      }
      img {
        width: 100%;
        display: block;
      }
    }
  }
}
