.account-section {
  @include wc-section-padding;
  .content {
    @include content;
    .woocommerce-MyAccount-navigation {
      margin-bottom: var(--padding-medium);
      ul {
        display: flex;
        justify-content: center;
        li {
          padding: 0 var(--padding-tiny);
          &:before {
            content: none;
          }
          &.is-active {
            a {
              @extend .btn-primary;
            }
          }
          a {
            @extend .btn-primary-alt;
          }
        }
      }
    }
    .woocommerce-EditAccountForm {
      max-width: 800px;
      margin: auto;
    }
    .woocommerce-MyAccount-content {
      max-width: 800px;
      margin: auto;
    }
    .woocommerce-MyAccount-content {
      mark {
        font-weight: 700;
      }
    }

    .edit-addresses-form {
      max-width: 800px;
      margin: auto;
    }
    .base-text {
      max-width: 1000px;
      margin: auto;
      text-align: center;
    }
  }
}
