.woocommerce-notices-wrapper {
  @include content;
  position: relative;
  display: flex;
  justify-content: center;
  .woocommerce-message, .woocommerce-error {
    border-radius: 32px;
    border: 2px solid $color-lred;
    font-weight: 700;
    padding: var(--padding-small);
    color: $color-text;
    text-align: center;
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    a {
      color: $color-lred;
      margin-left: 4px;
    }
  }
  .woocommerce-error {
    color: $color-white;
    background-color: $color-lred;
  }
}
