form, .form {

  input[type="text"], input[type="tel"], input[type="number"], input[type="password"], input[type="email"], input[type="file"], textarea, select {
    --border-color: #{$color-primary};
    appearance: none;
    border: 1px solid var(--border-color);
    // border-radius: var(--btn-round);
    background: none;
    padding: .7vw .6vw;
    font-family: $secondary-font-face;
    line-height: 1;
    font-size: 1.1em;
    color: var(--border-color);
    font-family: $primary-font-face;
    background-color: $color-white;
    width: 100%;
    &.white-input {
      --border-color: #{$color-white};
    }
    &::placeholder {
      // color: ligten($color-text, 50%);
      color: #ccc;
      font-weight: 300;
    }
    &:focus {
      outline: 0;
    }
    @media (max-width: $xsmall) {
      padding: 6px 15px;
    }
  }
  select {
    color: $color-text;
  }
  textarea {
    resize: none;
    min-height: 8vw;
    @media (max-width: $xsmall) {
      min-height: 120px;
    }
  }
  input[type="submit"] {
    @extend .btn;
    @extend .btn-primary;
  }
  label {}
}
