.wc-message-section {
  height: 30vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: $color-text;
  font-weight: 600;
  opacity: .8;
  font-size: 1.2em;
  &.larger {
    height: 36vh;
  }
  a {
    color: $color-red;
  }
  .woocommerce-notices-wrapper {
    display: none;
  }
}
