.wc-login-section {
  padding: var(--padding-large) var(--padding-sides-small);
  .box {
    max-width: 500px;
    margin: auto;
    .section-title {
      margin-bottom: var(--padding-small);
    }
    form {
      grid-template-columns: 1fr;
      .input-wrapper {
        &.submit {
          button {
            margin: auto;
            display: block;
          }
        }
        &.lost-pass {
          display: flex;
          justify-content: center;
        }
      }
    }
  }
}
