.two-cols-text {
  padding: var(--padding-medium) var(--padding-sides-small);
  .content {
    max-width: 64vw;
    margin: auto;
    column-gap: var(--padding-medium);
    column-count: 2;
    // text-align: justify;
    @media (max-width: $small) {
      column-count: 1;
      max-width: none;
    }
  }
}
