.red-js-form {
  * {
    box-sizing: border-box;
  }
  &-control-wrapper {
    position: relative;
    // margin-bottom: 15px;
    width: 100%;
    // display: flex;
    label {
      display: inline;
    }
  }
  &-errors {
    color: $color-error;
    display: none;
    padding: 15px;
    p {
      margin: 0;
    }
  }
  &-control {
    // padding: 5px 10px;
    display: block;
    // border: 1px solid #ccc;
    width: 100%;
    &[type="checkbox"] {
      width: auto;
      display: inline-block;
      margin-right: 5px;
    }
    &.recaptcha {
      // border: 0;
      // padding: 15px 0;
    }
    &:focus {
      outline: 0;
    }
    &.valid {
      // border-color: $color-success!important;
      // box-shadow: inset 0 0 0 1px $color-success!important;
    }
    &.invalid {
      // border-color: $color-error!important;
      // box-shadow: inset 0 0 0 1px $color-error!important;
    }
    &[type="submit"] {
      width: auto;
      // margin: auto;
      cursor: pointer;
    }
  }
  &-icon {
    width: 30px;
    height: 30px;
    // left: 100%;
    right: 12px;
    top: 10px;
    // margin-top: -15px;
    margin-left: 5px;
    position: absolute;
    background-size: 22px;
    // background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    display: none;
    @media (max-width: $xsmall) {
      right: 2px;
      top: 1px;
    }
    &:hover {
      .red-js-form-message {
        @media (max-width: $medium) {
          display: none!important;
        }
      }
    }

