    > .red-js-form-message {
      position: absolute;
      left: 100%;
      padding: 5px 10px;
      // width:
      top: 0;
      margin-left: 5px;
      min-width: 200px;
      min-height: 32px;
      display: none;
      z-index: 99;
      p {
        font-size: 16px;
        margin: 0;
        color: #fff;
        font-weight: 300;
        letter-spacing: .5px;
        text-align: center;
        align-items: center;
        line-height: 1.4;
      }
      &:before {
        content:"";
        border-style: solid;
        border-width: 5px 5px 5px 0;
        position: absolute;
        left: -5px;
        top: 10px;
      }
    }
