.op-hours {
  display: grid;
  width: 100%;
  grid-template-columns: 40% auto;
  grid-gap: 0 var(--padding-tiny);
  @media (max-width: $xsmall) {
    grid-gap: 0 15px;
  }
  .days {
    text-align: right;
    color: $color-red;
    p {
      font-weight: 600;
    }
  }
  .hours {
    text-align: left;
  }
}
