.woocommerce-Addresses, .woocommerce-columns--addresses {
  display: flex;
  justify-content: center;
  // align-items: center;
}
.woocommerce-Address, .woocommerce-column--shipping-address, .woocommerce-column--billing-address {
  padding: var(--padding-small);
  margin: 0 var(--padding-small);
  min-width: 300px;
  background-color: lighten($color-lgray, 5%);
}
.woocommerce-column--shipping-address, .woocommerce-column--billing-address {
  width: 50%;
  margin-top: var(--padding-medium);
  h2 {
    font-size: 1.6em;
  }
  p {
    font-size: 1rem;
    margin: 0;
  }
}

.woocommerce-shipping-calculator {
  display: none;
}

#billing_address_2_field, #shipping_address_2_field {
  display: none!important;
}
