.contact-form {
  @include bg-center;
  padding: var(--padding-large) var(--padding-sides-small);
  background-image: url('../img/_tmp/contact-form-bg.jpg');
  padding-top: 5vw;
  .content {
    @include content;
    .section-title {
      h1 {
        span {
          color: $color-white;
        }
      }
      p {
        color: $color-white;
      }
    }
    form {
      display: grid;
      grid-template-columns: 1fr 2fr;
      grid-gap: var(--padding-small);
      width: 60vw;
      margin: auto;
      margin-top: var(--padding-medium);
      grid-template-areas:
        "firstname topic"
        "lastname message"
        "date message"
        "phone message"
        "legal legal"
        "submit submit";
      @media (max-width: $medium) {
        width: 100%;
      }
      @media (max-width: $xsmall) {
        grid-template-areas:
          "firstname"
          "lastname"
          "phone"
          "topic"
          "date"
          "message"
          "legal"
          "submit";
        grid-template-columns: 1fr;
      }
      .input-wrapper {
        display: flex;
        .red-js-form-control-wrapper {
          display: flex;
        }
        &.firstname {
          grid-area: firstname;
        }
        &.lastname {
          grid-area: lastname;
        }
        &.topic {
          grid-area: topic;
        }
        &.phone {
          grid-area: phone;
        }
        &.date {
          grid-area: date;
        }
        &.message {
          grid-area: message;
        }
        &.legal {
          padding-top: var(--padding-small);
          display: block;
          font-size: .75em;
          grid-area: legal;
          @media (max-width: $medium) {
            font-size: .9em;
          }
        }
        &.submit {
          padding-top: var(--padding-small);
          justify-content: center;
          grid-area: submit;
          flex-direction: column;
          align-items: center;
          .red-js-form-control-wrapper {
            display: flex;
            justify-content: center;
            margin-bottom: var(--padding-small);
          }
          .recaptcha {
            display: flex;
            justify-content: center;
          }
          input {
            font-size: 1.8em;
            background-color: $color-red;
            @media (max-width: $xsmall) {
              font-size: 1.2em;
            }
          }
        }
      }
    }
  }
}
