/// Make image responsive
@mixin img-responsive {
  max-width: 100%;
}

@mixin bg-center {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
};

@mixin content {
  max-width: var(--content-width);
  margin: auto;
}

@mixin shadow {
  box-shadow: 0 1vw 2vw rgba($color-black, .2);
}

@mixin grid-bg {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-image: url('../img/bg-pat-gray.svg');
  background-size: 10vw;
  &:before {
    content: '';
    display: block;
    left: 0;
    right: 0;
    top: 0;
    height: 100%;
    background: linear-gradient( $color-white 15%, rgba($color-white, 0) );
  }
}

@mixin header-box {
  display: inline-block;
  position: relative;
  font-size: 5vw;
  color: $color-text;
  z-index: 0;
  line-height: .8;
  font-family: $secondary-font-face;
  font-weight: 300;
  @media (max-width: $medium) {
    font-size: 2.4rem;
  }
  &:after {
    z-index: -1;
    top: 70%;
    left: 4vw;
    content: '';
    display: block;
    background-color: $color-gold;
    width: 100%;
    height: 4vw;
    position: absolute;
  }
}
@mixin header-box-small {
  @include header-box;
  font-size: 3.6em;
  font-weight: 900;
  &:after {
    height: 2.4vw;
    left: 1.6vw;
  }
}

@mixin badge {
  display: inline-block;
  padding: .15vw .4vw;
  background-color: $color-gold;
  color: $color-text;
  border-radius: var(--btn-round);
  @media (max-width: $xsmall) {
    padding: 2px 10px;
  }
}

@mixin wc-section-padding {
  padding: var(--padding-medium) var(--padding-sides-small) var(--padding-large);
}
