.products-section {
  padding: var(--padding-medium) var(--padding-sides-small) var(--padding-large);
  // padding-bottom: 7vw;
  .content {
    --cols: 3;
    @include content;
    display: grid;
    grid-gap: var(--padding-large) var(--padding-small);
    grid-template-columns: repeat(var(--cols), 1fr);
    @media (max-width: $medium) {
      --cols: 2;
    }
    @media (max-width: $xsmall) {
      --cols: 1;
    }
  }
}
