.contact-section {
  background-color: $color-lgray;
  padding: var(--padding-large) var(--padding-sides-small);
  position: relative;
  &:before {
    content: '';
    background-image: url(#{$base64-contact-bg});
    background-repeat: no-repeat;
    background-position: top center;
    background-size: 16vw;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: .6;
  }
  .content {
    @include content;
    position: relative;
    z-index: 10;
    .cols {
      --cols: 2;
      display: grid;
      grid-template-columns: repeat(var(--cols), 1fr);
      grid-gap: var(--padding-small);
      @media (max-width: $medium) {
        --cols: 1;
      }
      .item {
        display: grid;
        grid-gap: var(--padding-small);
        justify-items: center;
        @media (max-width: $xsmall) {
          text-align: center;
        }
        p.lead {
          color: $color-lred;
          font-size: 1.6em;
          font-weight: 600;
        }
        p {
          font-size: 1.1em;
        }
      }
    }
  }
}
