$base64-wp: 'data:image/svg+xml;base64,';
$base64-dashboard: 'data:image/svg+xml;base64,';
$base64-edit: 'data:image/svg+xml;base64,';
$base64-add: 'data:image/svg+xml;base64,';
$base64-personalize: 'data:image/svg+xml;base64,';
$base64-logout: 'data:image/svg+xml;base64,';
@keyframes ball-in {
  0% {
    transform: scale(0);
    opacity: 0;
    filter: blur(8px);
  }
  100% {
    transform: scale(1);
    opacity: 1;
    filter: blur(0);
  }
}
#wpadminbar {
  position: fixed;
  top: 10px;
  left: 10px;
  width: 50px;
  height: 50px;
  background-image: url($base64-wp);
  background-position: center;
  background-size: 40px;
  transition: all .1s ease-out;
  background-repeat: no-repeat;
  border-radius: 50%;
  background-color: #fff;
  opacity: .7;
  transition: all .2s ease-out;
  cursor: pointer;
  z-index: 99999;
  background-size: 50px;
  &:hover {
    background-size: 50px;
    opacity: 1;
    #wp-toolbar {
      // display: block;
      opacity: 1;
      pointer-events: all;
      transition-delay: 0s;
      > ul {
        > li {
          animation-name: ball-in;
          animation-duration: .5s;
          animation-fill-mode: forwards;
          opacity: 0;
          position: relative;
          &:nth-child(1) {animation-delay: 0s; z-index: 10;}
          &:nth-child(2) {animation-delay: .1s; z-index: 9;}
          &:nth-child(3) {animation-delay: .2s; z-index: 8;}
          &:nth-child(4) {animation-delay: .3s; z-index: 7;}
          &:nth-child(5) {animation-delay: .4s; z-index: 6;}
          &:nth-child(6) {animation-delay: .5s; z-index: 5;}
          &:nth-child(7) {animation-delay: .6s; z-index: 4;}
          &:nth-child(8) {animation-delay: .7s; z-index: 3;}
        }
      }
    }
  }
  .screen-reader-shortcut {
    display: none;
  }
  #wp-toolbar {
    width: 50px;
    height: 50px;
    position: relative;
    // display: none;
    opacity: 0;
    pointer-events: none;
    transition: all .5s ease-out;
    transition-delay: .2s;
    &:focus {
      outline: 0;
    }
    &:before {
      content: '';
      position: absolute;
      display: block;
      left: -15px;
      top: -15px;
      right: -15px;
      bottom: -15px;
    }
    > ul {
      position: absolute;
      &:before {
        content: '';
        display: block;
        position: absolute;
        bottom: -20px;
        left: -20px;
        right: -20px;
        top: -15px;
      }
      li {
        &:hover {
          &:after {
            opacity: 1;
            transition-delay: .3s;
            left: 100%;
            filter: blur(0);
          }
        }
        &:after {
          display: block;
          position: absolute;
          left: 50%;
          top: 50%;
          padding: 5px 8px;
          background-color: #333;
          color: #fff;
          text-align: center;
          pointer-events: none;
          opacity: 0;
          transition: all .2s ease-out;
          box-sizing: border-box;
          height: 20px;
          margin-top: -10px;
          font-size: 9px;
          z-index: 9999;
          border-radius: 20px;
          margin-left: 2px;
          filter: blur(5px);
        }
      }
      &#wp-admin-bar-root-default {
        z-index: 10;
        left: 65px;
        height: 50px;
        top: 0;
        display: flex;
        > li {
          width: 50px;
          height: 50px;
          border-radius: 50%;
          background-color: #00769D;
          margin-right: 15px;
          position: relative;
          display: none;
          justify-content: center;
          align-items: center;
          transition: background-color .2s ease-out;
          position: relative;
          &:hover {
            background-color: #35a5c6;
            transition-delay: .4s;
            transition: filter .2s ease-out;
            // filter: blur(5px);
          }
          > a {
            padding: 10px;
            width: 100%;
            display: flex;
            align-items: center;
            text-align: center;
            font-size: 8px;
            width: 100%;
            height: 100%;
            background-position: center;
            background-size: 25px;
            background-repeat: no-repeat;
            color: transparent;
          }
        }
        .menupop {
          position: relative;
          &.hover {
            .ab-sub-wrapper {
              // display: block;
              opacity: 1;
              pointer-events: all;
              ul {
                li {
                  transform: translateY(0);
                  opacity: 1;
                }
              }
            }
          }
          .ab-sub-wrapper {
            opacity: 0;
            pointer-events: none;
            // display: none;
            position: absolute;
            top: 100%;
            left: 50%;
            width: 100px;
            margin-left: -50px;
            z-index: 10;
            transition: all .4s ease-out;
            &:before {
              content: '';
              display: block;
              position: absolute;
              bottom: -20px;
              left: -20px;
              right: -20px;
              top: 0;
              // outline: 1px solid red;
            }
            ul {
              li {
                transform: translateY(-4px);
                opacity: 0;
                transition: all .15s ease-out;
                &:nth-child(1) {transition-delay: .05s;}
                &:nth-child(2) {transition-delay: .1s;}
                &:nth-child(3) {transition-delay: .15s;}
                &:nth-child(4) {transition-delay: .2s;}
                &:nth-child(5) {transition-delay: .25s;}
                a {
                  margin-top: 5px;
                  display: block;
                  padding: 5px;
                  color: #fff;
                  background-color: #333;
                  text-align: center;
                  border-radius: 12px;
                  position: relative;
                  z-index: 10;
                  &:hover {
                    color: #fff;
                    background-color: #ffa500;
                  }
                }
              }
            }
          }
        }
        #wp-admin-bar-site-name {
          display: flex;
          &:after {content: 'Dashboard';}
          > a {
            background-image: url($base64-dashboard);
          }
        }
        #wp-admin-bar-new-content {
          &:after {content: 'Dodaj';}
          display: flex;
          > a {
            background-image: url($base64-add);
            .ab-icon {}
            .ab-label {}
          }
          .ab-sub-wrapper {}
        }
        #wp-admin-bar-customize {
          &:after {content: 'Porsonalizacja';}
          display: flex;
          a {
            background-image: url($base64-personalize);
          }
        }
        #wp-admin-bar-edit {
          &:after {content: 'Edycja';}
          display: flex;
          background-color: #ff3700;
          &:hover {
            background-color: lighten(#ff3700, 10%);
          }
          a {
            background-image: url($base64-edit);
          }
        }
      }
      &#wp-admin-bar-top-secondary {
        top: 100%;
        left: 0;
        display: flex;
        flex-direction: column;
        width: 50px;
        opacity: .4;
        transition: all .2s ease-out;
        padding-top: 15px;
        &:hover {
          opacity: 1;
        }
        #wp-admin-bar-my-account {
          position: relative;
          &:after {content: 'Wyloguj';}

          > a {
            display: none;
          }
          .ab-sub-wrapper {
            #wp-admin-bar-user-actions {
              #wp-admin-bar-user-info {
                display: none;
              }
              #wp-admin-bar-edit-profile {
                display: none;
              }
              #wp-admin-bar-logout {
                width: 50px;
                height: 50px;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: #555;
                border-radius: 50%;

                a {
                  display: block;
                  width: 100%;
                  height: 100%;
                  background-position: center;
                  background-size: 25px;
                  background-repeat: no-repeat;
                  color: transparent;
                  background-image: url($base64-logout);
                }
              }
            }
          }
        }
      }
    }
  }
}
