#main-navbar {
  --brand-width: 13vw;
  height: 12.5vw;
  @media (max-width: $small) {
    height: auto;
  }
  &.fixed {
    @media (min-width: $small) {
      .content {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        z-index: 999;
        background-color: $color-white;
        box-shadow: 0 0 1vw rgba($color-black, .1);
        max-width: none;
        padding: var(--padding-small) 5vw;

        grid-template-areas: "nav-left brand nav-right shop";
        grid-template-columns: 4fr 3vw 3fr 1fr;
        grid-template-rows: auto;
        border-bottom: 0;
        .text {
          display: none;
        }
        .brand {
          display: flex;
          align-items: center;
          justify-content: center;
          img {
            display: block;
            width: 100%;
            &.logo,
            &.bg {
              display: none;
            }
            &.scroll {
              display: block;
              width: 100%;
            }
          }
        }
        .shop {
          .scroll-hide {
            display: none;
          }
        }
        ul.sub-menu {
          top: 3.5vw!important;
        }
      }
    }
  }
  .content {
    @include content;
    display: grid;
    grid-template-columns: 1fr var(--brand-width) 1fr;
    grid-template-rows: calc( var(--brand-width) * .5 ) calc( var(--brand-width) * .3 );
    grid-gap: var(--padding-small);
    grid-template-areas:
      "text brand shop"
      "nav-left brand nav-right";
    border-bottom: 1px solid $color-primary;
    padding-bottom: var(--padding-small);
    @media (max-width: $small) {
      position: fixed;
      display: flex;
      justify-content: space-between;
      align-items: center;
      z-index: 9999;
      background-color: $color-white;
      left: 0;
      right: 0;
      top: 0;
      max-width: none;
      border: 0;
      padding: var(--padding-small);
      box-shadow: 0 0 15px rgba($color-black, .1);
    }
    .text,
    .shop {
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: $small) {
        display: none;
      }
    }
    .text {
      grid-area: text;
      p {
        color: $color-primary;
      }
      a {
        margin-left: var(--padding-small);
        color: darken($color-primary, 60%);
        font-weight: 700;
        font-size: 1.15em;
      }
    }
    .brand {
      grid-area: brand;
      position: relative;
      img {
        display: block;
        width: 100%;
        &.logo {
          position: relative;
          z-index: 10;
          @media (max-width: $small) {
            display: none;
          }
        }
        &.bg {
          position: absolute;
          top: 0;
          left: 0;
          @media (max-width: $small) {
            display: none;
          }
        }
        &.scroll {
          display: none;
          @media (max-width: $small) {
            display: block;
            width: 40px;
          }
        }
      }
    }
    .shop {
      grid-area: shop;
      @media (max-width: $small) {
        display: none;
      }
      .shop-menu {
        display: grid;
        grid-auto-flow: column;
        align-items: center;
        grid-gap: var(--padding-small);
        .cart-link {
          position: relative;
          .ico {}
          .cart-count {
            position: absolute;
            background-color: $color-red;
            border-radius: 1vw;
            min-width: .9vw;
            height: .9vw;
            color: $color-white;
            display: flex;
            text-align: center;
            justify-content: center;
            line-height: 1.65;
            font-size: .7em;
            font-weight: 600;
            right: -.5vw;
            bottom: -.8vw;
            flex-shrink: 0;
            padding: 0 4px;
            transform: scale(.85);
            @media (max-width: $xlarge) {
              min-width: 1.3vw;
              height: 1.3vw;
            }
          }
        }
      }
      .social {
        margin-left: 2vw;
        position: relative;
        &:before {
          content: '';
          display: block;
          width: 1px;
          top: 0;
          bottom: 0;
          left: -1vw;
          position: absolute;
          background-color: $color-text;
          opacity: .4;
        }
      }
    }
    .nav-left,
    .nav-right {
      display: flex;
      justify-content: center;
      // align-items: center;
      @media (max-width: $small) {
        display: none;
      }
      > ul {
        display: grid;
        grid-gap: var(--padding-small);
        grid-template-columns: repeat(3, 1fr);
        width: 100%;
        > li {
          display: flex;
          position: relative;
          &:before {
            content: '';
            display: block;
            position: absolute;
            // background-color: red;
            left: 0;
            right: 0;
            top: 100%;
            height: 20px;
            z-index: 10;
          }
          &:after {
            --width: 3vw;
            content: '';
            display: block;
            position: absolute;
            bottom: 0;
            left: 50%;
            border-radius: 2px;
            width: 3vw;
            margin-left: calc( var(--width) / -2 );
            height: .3vw;
            margin-bottom: calc( var(--padding-small) * -1 );
            background-color: $color-lred;
            opacity: 0;
          }
          &.current-menu-item, &.current-menu-parent, &:hover {
            &:after {
              opacity: 1;
            }
          }
          &:hover {
            ul.sub-menu {
              opacity: 1;
              pointer-events: all;
            }
          }
          > a {
            display: block;
            text-transform: uppercase;
            font-weight: 600;
            color: $color-text;
            font-size: 1.2em;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
          }
          ul.sub-menu {
            position: absolute;
            left: 0;
            // right: 0;
            top: 4.65vw;
            padding: 1.2vw 2vw;
            display: grid;
            grid-gap: .6vw;
            background-color: $color-lred2;
            z-index: 10;
            opacity: 0;
            pointer-events: none;
            li {
              &.current-menu-item {
                a {
                  color: $color-white;
                }
              }
              a {
                white-space: nowrap;
                display: block;
                color: darken($color-red, 40%);
                font-weight: 500;
                font-size: 1.2em;
                &:hover {
                  color: $color-white;
                }
              }
            }
          }
        }
      }
    }
    .nav-left {
      grid-area: nav-left;
    }
    .nav-right {
      grid-area: nav-right;
    }
    .hamburger {
      display: none;
      @media (max-width: $small) {
        display: block;
      }
      .icon {
        --size: 32px;
        width: var(--size);
        height: var(--size);
        position: relative;
        cursor: pointer;
        &.close {
          span {
            top: calc( var(--size)/2 - 2px )!important;
            &:nth-child(1) {
              transform: rotateZ(45deg);
            }
            &:nth-child(2) {
              opacity: 0;
            }
            &:nth-child(3) {
              transform: rotateZ(-45deg);
            }
          }
        }
        span {
          position: absolute;
          display: block;
          width: 32px;
          height: 3px;
          background-color: $color-primary;
          top: calc( var(--size)/2 - 2px );
          left: 0;
          &:nth-child(1) {
            top: calc( var(--size)/2 - 2px - 8px );
          }
          &:nth-child(2) {}
          &:nth-child(3) {
            top: calc( var(--size)/2 - 2px + 8px );
          }
        }
      }
    }
  }
}
