.e404 {
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: $small) {
    height: 82vh;
  }
  @media (max-width: $xsmall) {
    height: auto;
    padding: 100px 0;
  }

  .box {
    .text-wrapper {
      text-align: center;

      h2 {
        font-size: 12em;
        color: $e404-font-color;
        font-weight: 700;
        line-height: 1;
        @media (max-width: $xsmall) {
          font-size: 7em;
        }
      }

      h5 {
        color: $e404-font-color-alt;
        font-weight: 400;
      }
    }

    .back {
      margin-top: 100px;
      text-align: center;
      @media (max-width: $xsmall) {
        margin-top: 30px;
      }

      a {}
    }
  }
}
