.full-width-links {
  --cols: 2;
  display: grid;
  grid-template-columns: repeat(var(--cols), 1fr);
  @media (max-width: $xsmall) {
    --cols: 1;
  }
  .item {
    position: relative;
    display: none;
    // text-align: center;
    &:before {
      content: '';
      display: block;
      width: 70%;
      height: 1vw;
      background-color: $color-lred;
      position: absolute;
      z-index: 10;
      bottom: 0;
    }
    &:nth-child(1) {
      display: block;
      .text {
        justify-content: flex-end;
        text-align: right;
      }
    }
    &:nth-child(2) {
      display: block;
      &:before {
        right: 0;
      }
    }
    .text {
      display: flex;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 10;
      padding: 2.4vw;
      p {
        font-size: 4.6em;
        font-weight: 900;
        text-transform: uppercase;
        letter-spacing: .4vw;
        color: $color-lred;
        width: 60%;
        text-shadow:
          .13vw .13vw 1px $color-white,
          -.13vw -.13vw 1px $color-white,
          -.13vw .13vw 1px $color-white,
          .13vw -.13vw 1px $color-white;
        @media (max-width: $small) {
          font-size: 3em;
        }
      }
    }
    &:after {
      content: '';
      display: block;
      padding-bottom: 50%;
    }
    img {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
