.woocommerce-pagination {
  padding-bottom: 5vw;
  .page-numbers {
    display: flex;
    justify-content: center;
    align-items: center;
    .page-numbers {
      --size: 3vw;
      height: var(--size);
      width: var(--size);
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 1vw;
      border-radius: 50%;
      border: 2px solid $color-green;
      font-size: 1.4em;
      font-weight: 700;
      color: $color-green;
      @media (max-width: $large) {
        --size: 4vw;
      }
      @media (max-width: $small) {
        --size: 32px;
      }
      &.prev,
      &.next {
        border: 0;
        font-size: 3em;
        font-weight: 300;
        margin-top: -.4vw;
      }
      &.current {
        background-color: $color-green;
        color: $color-white;
      }
    }
  }
}
