.category-select {
  padding: var(--padding-large) var(--padding-sides-small);
  @media (max-width: $xsmall) {
    padding-top: 0;
    padding-bottom: 0;
  }
  &.alt {
    .content {
      grid-gap: var(--padding-medium);
      .item {
        background-color: $color-white;
        box-shadow: 0 0 .4vw rgba($color-black, .2);
        @media (min-width: $xsmall) {
          &:hover {
            transform: scale(1.03);
          }
        }
      }
    }
  }
  .content {
    --cols: 3;
    @include content;
    display: grid;
    grid-template-columns: repeat(var(--cols), 1fr);
    grid-gap: var(--padding-large);
    @media (max-width: $xsmall) {
      --cols: 1;
      grid-gap: 0;
    }
    .item {
      display: block;
      border-radius: 2vw;
      padding: 2vw 1vw;
      box-shadow: 0 0 2vw rgba($color-black, 0);
      transition: all .2s ease-out;
      background-color: $color-white;
      @media (min-width: $xsmall) {
        &:hover {
          transform: scale(1.1);
          box-shadow: 0 0 1vw rgba($color-black, .2);
          p {
            color: $color-red;
            opacity: .3;
          }
        }
      }
      img {
        display: block;
        width: 100%;
        height: 23vw;
        object-fit: contain;
        @media (max-width: $xsmall) {
          height: 120px;
        }
      }
      p {
        opacity: .7;
        letter-spacing: .2em;
        height: 5vw;
        display: flex;
        align-items: center;
        justify-content: center;
        // font-size: 4.8em;
        // font-size: 3.8vw;
        font-size: 2.5vw;
        color: $color-green;
        font-weight: 900;
        text-transform: uppercase;
        text-align: center;
        @media (max-width: $xsmall) {
          font-size: 1.8em!important;
          height: auto;
        }
      }
    }
  }
}
