.cart-section {
  @include wc-section-padding;
  .shipping-calculator-form {
    display: grid;
    grid-gap: var(--padding-small);
    margin-top: var(--padding-small);
  }
  .content {
    @include content;
    .cart-empty {

    }
    .cart-table {
      width: 100%;
      color: $color-text;
      tr.cart_item,
      thead tr {
        border-bottom: 1px solid lighten($color-gray, 50%);
      }
      td, th {
        padding: 1vw;
				vertical-align: middle;
        text-align: center;
      }
      thead {
        tr {
          th {
            text-transform: uppercase;
            font-weight: 700;
          }
        }
      }
      tbody {
        tr {
          td {
            &.product-remove {
              a {
                font-size: 4em;
                font-weight: 100;
                color: $color-red;
              }
            }
            &.product-thumbnail {
              img {
                max-width: 200px;
                max-height: 120px;
                object-fit: contain;
              }
            }
            &.product-name {
              font-size: 1.2em;
              font-weight: 700;
              // display: flex;
              // flex-direction: column;
              // align-items: center;
              img {
                order: 3;
                width: 220px;
              }
              .extra-data {
                order: 2;
              }
              a {
                order: 1;
                color: $color-red;
              }
              .extra-data {
                p {
                  font-size: .8em;
                  color: $color-gray;
                  font-weight: 500;
                  margin-bottom: 5px;
                }
                ul {
                  display: grid;
                  grid-gap: 4px;
                  li {
                    font-size: .7em;
                    color: $color-gray;
                    font-weight: 400;
                  }
                }
              }
            }
            &.product-price {
              font-size: 1.2em;
              font-weight: 700;
            }
            &.product-quantity {
              .quantity {
                display: flex;
                justify-content: flex-end;
                align-items: center;
              }
              label {
                font-weight: 600;
              }
              input {
                max-width: 70px;
                margin-left: 8px;
              }
            }
            &.product-subtotal {
              font-size: 2em;
              font-weight: 700;
            }
          }
        }
      }
    }
    .coupon {
      max-width: 400px;
      margin: auto;
      display: grid;
      grid-gap: var(--padding-small);
      margin-bottom: var(--padding-small);
      label {}
      .input-wrapper {
        display: grid;
        grid-template-columns: 2fr 1fr;
        grid-gap: var(--padding-small);
      }
    }
    .cart-collaterals {
      h2 {
        text-align: center;
        font-size: 1.4em;
        margin-bottom: var(--padding-small);
      }
      table {
        margin: auto;
        a {
          color: $color-red;
        }
        tbody {
          tr {
            // display: grid;
            // grid-template-columns: 10% auto;
            // grid-gap: var(--padding-small);
            margin-bottom: var(--padding-small);
            &.order-total {
              td {
                font-size: 2em;
                color: $color-red;
                font-weight: 600;
              }
            }
            th,
            td {
      				vertical-align: middle;
              padding: var(--padding-small);
              // text-align: center;
            }
            th {}
            td {}
          }
        }
      }
      .wc-proceed-to-checkout {
        display: flex;
        justify-content: flex-end;
        a {
          @extend .btn;
          @extend .btn-primary;
        }
      }
    }
  }
}
