.product-card {
  padding: var(--padding-large) var(--padding-sides-small);
  padding-bottom: 7vw;
  .content {
    position: relative;
    @include content;
    display: grid;
    grid-template-columns: 40% auto;
    grid-gap: var(--padding-medium);
    @media (max-width: $medium) {
      grid-template-columns: 100%;
    }
    .category-icon {
      position: absolute;
      right: 0;
      top: -3vw;
      @media (max-width: $xsmall) {
        display: none;
      }
      img {
        opacity: .6;
        width: 5vw;
      }
    }
    .images {
      .big-photo {
        margin-bottom: var(--padding-small);
        img {
          width: 100%;
          height: 24vw;
          object-fit: contain;
          @media (max-width: $small) {
            height: 50vw;
          }
        }
      }
      .gallery {
        position: relative;
        padding: 0 1.5vw;
        .navigation {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          z-index: 10;
          pointer-events: none;
          .nav {
            --size: 3vw;
            height: var(--size);
            width: var(--size);
            pointer-events: all;
            border-radius: 50%;
            background-color: $color-white;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: .2vw;
            cursor: pointer;
            &:focus {
              outline: 0;
            }
            &.prev {}
            &.next {}
            &.swiper-button-disabled {
              .icon {
                background-color: $color-lgray;
              }
            }
            .icon {
              background-color: $color-lred2;
              width: 100%;
              height: 100%;
              border-radius: 50%;
              display: flex;
              justify-content: center;
              align-items: center;
              .ico {
                width: 1vw;
              }
            }
          }
        }
        .swiper-slide {
          .slide-image {
            background-color: $color-lgreen;
            position: relative;
            &:before {
              content: '';
              display: block;
              padding-bottom: 70%;
              width: 100%;
            }
            img {
              position: absolute;
              left: 0;
              top: 0;
              height: 100%;
              width: 100%;
              object-fit: cover;
              cursor: pointer;
              // display: none;
            }
          }
        }
      }
    }
    .text {
      .text-wrapper {
        h1 {
          font-weight: 900;
          text-transform: uppercase;
          color: $color-lred2;
          font-size: 3.7em;
          margin-bottom: var(--padding-medium);
          padding-right: 6vw;
          @media (max-width: $medium) {
            font-size: 2.2em;
          }
        }
      }
      > .price {
        display: none;
      }
      > .cart {
        input {
        }
        button {
          // @extend .btn;
          // @extend .btn-primary;
          // background-color: $color-red;
          // border-color: $color-red;
          // text-transform: uppercase;
        }
      }
      .variant-select-title {
        padding: var(--padding-small) 0;
        p {
          font-weight: 600;
          font-size: 1.3em;
          color: $color-text;
        }
      }
      .variant-select {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-bottom: 2vw;
        .item {
          width: 30%;
          border-radius: 50px;
          border: 2px solid $color-lgreen;
          padding: .5vw 1vw;
          margin: 0 .5vw;
          text-align: center;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          cursor: pointer;
          min-width: 160px;
          margin-bottom: var(--padding-small);
          &.active {
            background-color: $color-lgreen;
            * {
              color: $color-white;
            }
          }
          p {
            color: $color-text;
          }
          .label {
            display: grid;
            grid-gap: .2vw;
            margin-bottom: .2vw;
            p {
              &.variant {
                text-transform: uppercase;
                font-weight: 700;
                font-size: 2em;
                @media (max-width: $small) {
                  font-size: 1.5em;
                }
              }
              &.desc {
                margin-left: .2vw;
                font-weight: 500;
                font-size: 1.2em;
                white-space: nowrap;
                @media (max-width: $xsmall) {
                  margin-left: 5px;
                }
              }
            }
          }
          .price {
            del {
              font-size: .6em;
            }
            ins {
              text-decoration: none;
              font-size: .9em;
            }
            p {
              font-weight: 800;
              font-size: 2em;
              @media (max-width: $small) {
                font-size: 1.5em;
              }
            }
          }
        }
      }
      #single-product-price {
        display: none;
      }
      .extra-product-data {
        display: grid;
        // grid-template-columns: repeat(2, 1fr);
        grid-gap: var(--padding-small);
        padding: var(--padding-small) 0 var(--padding-medium);
        .title {
          p {
            font-size: 1.4em;
            color: $color-text;
            font-weight: 600;
          }
        }
        .items {
            display: grid;
            grid-gap: 5px;
            .extra-product-data-input {
              display: grid;
              grid-gap: var(--padding-small);
              grid-template-columns: 12vw auto;
              @media(max-width: $small) {
                grid-template-columns: 100px auto;
              }
              @media(max-width: $xsmall) {
                grid-template-columns: auto;
              }
              &.active {
                .input {
                  opacity: 1;
                  pointer-events: all;
                  @media (max-width: $xsmall) {
                    display: block;
                  }
                }
              }
              .checkbox {
                display: grid;
                grid-template-columns: 2vw auto;
                grid-gap: var(--padding-small);
                align-items: center;
                cursor: pointer;
                @media (max-width: $xsmall) {
                  grid-template-columns: 32px auto;
                }
                &.active {
                  .box {
                    &:before {
                      opacity: 1;
                    }
                  }
                }
                .box {
                  --size: 1.8vw;
                  width: var(--size);
                  height: var(--size);
                  border-radius: .6vw;
                  border: 3px solid $color-lgreen;
                  flex-shrink: 0;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  @media (max-width: $xlarge) {
                    --size: 2vw;
                  }
                  @media (max-width: $xsmall) {
                    --size: 26px;
                    border-radius: 8px;
                  }
                  &:before {
                    --bullet-size: 49%;
                    content: '';
                    display: block;
                    width: var(--bullet-size);
                    height: var(--bullet-size);
                    background-color: $color-lgreen;
                    border-radius: 50%;
                    opacity: 0;
                  }
                }
                .label {
                  font-size: 1.2em;
                  font-weight: 500;
                  color: $color-text;
                }
              }
              .input {
                opacity: 0;
                pointer-events: none;
                transition: all .2s ease-out;
                display: grid;
                grid-gap: 5px;
                @media (max-width: $small) {
                  display: block;
                }
                @media (max-width: $xsmall) {
                  display: none;
                }
                &.file, &.text {
                  label {
                    margin-bottom: 5px;
                    display: block;
                  }
                }
                label {}
                input {
                  height: 1.8vw;
                  border-radius: 10px;
                  border: 3px solid $color-lgreen;
                  padding: 5px 15px;
                  font-family: $primary-font-face;
                  min-height: 36px;
                  @media (max-width: $small) {
                    height: 32px;
                    width: 100%;
                  }
                  &[type="number"] {
                    width: 84px;
                  }
                  &.text {
                    width: 100%;
                    max-width: 300px;
                  }
                  &:focus {
                    outline: none;
                  }
                  .image-preview {
                    img {
                      max-width: 100%;
                    }
                  }
                }
              }
            }
          }
      }
      .product-add-to-cart-section {
        display: grid;
        grid-template-columns: 2fr 1fr 2fr;
        grid-gap: var(--padding-small);
        align-items: center;
        width: 90%;
        @media (max-width: $large) {
          width: 100%;
        }
        @media (max-width: $xsmall) {
          grid-template-columns: 2fr 1fr;
          grid-gap: 20px;
        }
        .large-number {
          color: $color-lred;
          font-size: 3em;
          font-weight: 700;
          @media (max-width: $xsmall) {
            font-size: 3em;
          }
        }
        .label {
          font-size: 2em;
          color: $color-gray;
          font-weight: 600;
          text-transform: uppercase;
          margin-bottom: 5px;
        }
        .total {}
        .quantity {
          .quantity-select {
            display: flex;
            align-items: center;
            .number {
              width: 70px;
              text-align: center;
              p {}
            }
            .controls {
              display: grid;
              grid-gap: 3px;
              margin-left: 5px;
              .control-item {
                --size: 22px;
                width: var(--size);
                height: var(--size);
                background-color: $color-lgray;
                color: $color-text;
                display: flex;
                justify-content: center;
                align-items: center;
                font-weight: 700;
                font-size: 1.4em;
                cursor: pointer;
                &:hover {
                  background-color: $color-lgreen;
                }
                p {
                  pointer-events: none;
                }
              }
              .inc {}
              .dec {}
            }
          }
        }
        .add-to-cart {
          @media (max-width: $xsmall) {
            grid-column: span 2;
            display: flex;
            justify-content: center;
          }
        }
      }
      .variations_form.cart {
        // display: none;
      }
      .no-price-text {
        padding: var(--padding-small) 0;
        // text-align: center;
        h2 {
          text-transform: uppercase;
          font-weight: 700;
          color: $color-lgreen;
        }
      }
      .debug-section {
        padding: var(--padding-medium);
        background-color: lighten($color-lred, 22%);
        margin: 50px 0;
        display: none;
      }
      .hide-default-wc-cart {
        display: none;
        outline: 1px solid red;
        padding: 15px;
        background: repeating-linear-gradient(45deg, transparent,transparent 10px,#ffd4d4 10px,#ffd4d4 20px);
      }
    }
  }
}
