.visit-us {
  @include bg-center;
  padding: var(--padding-large) var(--padding-sides-small);
  min-height: 40vw;
  @media (max-width: $xsmall) {
    background-image: none!important;
    padding-top: 0;
    padding-bottom: 0;
  }
  .content {
    width: 50vw;
    margin: auto;
    text-align: center;
    padding-top: 5vw;
    @media (max-width: $xsmall) {
      width: 100%;
    }
    .section-title {
      margin-bottom: 2vw;
      h1 {
        strong {
          color: $color-lred;
        }
      }
    }
    .text {
      width: 70%;
      margin: auto;
      @media (max-width: $xsmall) {
        width: 100%;
      }
      p {
        font-size: 1.4em;
      }
    }
    .links {
      display: grid;
      grid-auto-flow: row;
      grid-gap: var(--padding-small);
      margin-top: 5vw;
      justify-content: center;
      @media (max-width: $xsmall) {
        grid-gap: 0;
      }
      .item {
        font-size: 2em;
        padding: var(--padding-small);
        color: $color-text;
        display: flex;
        align-items: center;
        justify-content: center;
        @media (max-width: $xsmall) {
          font-size: 1.6em;
        }
        &:after {
          --size: 2vw;
          margin-left: 1vw;
          content: '';
          display: block;
          width: var(--size);
          height: var(--size);
          background-color: $color-lred;
          border-radius: 50%;
          @extend .ico;
          @extend .ico-link-arrow;
          background-size: .5vw;
          @media (max-width: $large) {
            --size: 2.2vw;
            background-size: .6vw;
          }
          @media (max-width: $small) {
            --size: 3vw;
            background-size: .8vw;
          }
          @media (max-width: $xsmall) {
            --size: 26px;
            background-size: 8px;
          }
        }
      }
    }
  }
}
