.img-responsive {
  @include img-responsive();
}

.social {
  display: grid;
  grid-auto-flow: column;
  grid-gap: .5vw;
  @media (max-width: $navbar-break-point) {
    grid-gap: 15px;
  }
  a {
    --size: 1.6vw;
    width: var(--size);
    height: var(--size);
    border-radius: 50%;
    background-color: #ccc9c9;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: $navbar-break-point) {
      --size: 32px;
    }
    span {
      --scale: 60%;
      width: var(--scale);;
      height: var(--scale);;
    }
  }
}

.shipping-method-description {
  font-style: italic;
  font-size: .8em;
  margin-top: 8px;
}

#przelewy_payment_form {
  display: grid;
  grid-template-areas: 
    "cancel confirm"
    "info info";
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
    @media (max-width: $xsmall) {
      grid-template-columns: repeat(1, 1fr);
      grid-template-areas: 
        "cancel"
        "confirm"
        "info";
      grid-gap: 15px;
  }
  p {
    grid-area: info;
    font-weight: 700;
    @media (max-width: $xsmall) {
      text-align: center!important;
    }
  }
  #place_order {
    grid-area: confirm;
  }
  .button.cancel {
    grid-area: cancel;
    @extend .btn;
    @extend .btn-primary-alt;
  }
}


.woocommerce-shipping-totals.info {
  font-weight: 700;
}

.custom-time-order {
  .woocommerce-input-wrapper {
    display: grid;
    grid-template-columns: 20px auto;
    grid-gap: 5px;
    padding-top: 15px;
  }
}

.empty-spacer {
  height: 3vw;
  &.half {
    height: 1.5vw;
  }
  @media (max-width: $xsmall) {
    height: 0;
  }
}


.todo {
  color: red;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    display: block;
  }
}

.select2 {
  display: none!important;
}


